if ( !virtualInput ) {
    input.$node.addEventListener('keydown', function ( event ) {
        if ( page.activeComponent !== input ) {
            switch ( event.keyCode ) {
                case keys.f1:
                case keys.left:
                case keys.right:
                case keys.up:
                case keys.down:
                    event.preventDefault();
                    break;

                default:
                    break;
            }
        }
    });

    input.getCaretPosition = function () {
        console.log('getCaretPosition: ' + this.$node.selectionStart);

        return this.$node.selectionStart;
    };
}

window.core.addListener('hide', function () {
    if ( lastIntent ) {
        lastIntent.close();
    }
    window.core.call('hide');
});

window.core.addListener('intent', function ( intent, callback ) {
    var logoUrl;

    keyboard.focus();

    if ( intent.action === 'keyboard' ) {
        intent.once('close', function () {
            if ( popup.visible ) {
                popup.hide();
                keyboard.focus();
            }
        });
        lastIntent = intent;
        logoUrl = intent.runnerConfig.uris.icons.logoActive;

        if ( popup.visible ) {
            popup.hide();
            keyboard.focus();
        }

        lang.active = lang.codes.indexOf(window.core.environment.language);
        if ( lang.active === -1 ) {
            lang.active = 0;
        }
        radioList.checkIndex(lang.active);

        window.pageMainLabel.innerText = input.$node.placeholder = input.value = input.$node.value = '';
        window.pageMainIcon.style.backgroundImage = 'url(' + logoUrl + ')';
        window.pageMainLabel.innerText = intent.runnerConfig.name;
        input.$node.focus();

        switch ( intent.data.type ) {
            case 'password':
                window.pageMainHeadTitle.innerText = gettext('Enter the password');
                button.$text.innerText = gettext('Show password');
                button.show();
                verticalNavigation.list = [keyboard, button, input];
                verticalNavigation.active = 0;
                keyboard.focus();
                break;

            default:
                window.pageMainHeadTitle.innerText = gettext('Enter the data');
                button.hide();
                verticalNavigation.list = [keyboard, input];
                verticalNavigation.active = 0;
                keyboard.focus();
                break;
        }

        if ( intent.data ) {
            // prepare interface
            if ( virtualInput ) {
                switch ( intent.data.type ) {
                    case 'password':
                        input.init({
                            type: Input.prototype.TYPE_PASSWORD
                        });
                        break;

                    default:
                        input.init({
                            type: Input.prototype.TYPE_TEXT
                        });
                        break;
                }
            } else {
                switch ( intent.data.type ) {
                    case 'password':
                        input.$node.type = 'password';
                        break;

                    default:
                        input.$node.type = '';
                        break;
                }
            }

            if ( intent.data.title ) {
                window.pageMainHeadTitle.innerText = intent.data.title;
            }

            if ( intent.data.icon ) {
                window.pageMainIcon.style.backgroundImage = 'url(' + intent.data.icon + ')';
            }

            if ( intent.data.description ) {
                window.pageMainLabel.innerText = intent.data.description;
            }

            if ( intent.data.placeholder ) {
                input.$node.placeholder = intent.data.placeholder;
            }

            if ( intent.data.value ) {
                if ( virtualInput ) {
                    input.setValue(intent.data.value);
                } else {
                    input.value = input.$node.value = '' + intent.data.value;
                    lastInputCaretPosition = input.$node.selectionStart = input.$node.selectionEnd = input.value.length;
                }
            } else {
                if ( virtualInput ) {
                    input.setValue('');
                } else {
                    input.value = '';
                }
            }

            if ( intent.data.position ) {
                lastInputCaretPosition = input.$node.selectionStart = input.$node.selectionEnd = +intent.data.position;
            }

            if ( intent.data.language ) {
                if ( lang.codes.indexOf(intent.data.language) !== -1 ) {
                    lang.active = lang.codes.indexOf(intent.data.language);
                }
            }
        }

        switch ( intent.data.mode ) {
            case 'pin':
            case 'number':
                numericProvider.get(null, function ( error, data ) {
                    if ( !error ) {
                        keyboard.$node.classList.add('small');
                        if ( virtualInput ) {
                            input.changeView('small', 'add');
                        } else {
                            input.$node.classList.add('small');
                        }
                        keyboard.init({
                            data: data
                        });
                    }
                });
                break;

            default:

                qwertyProvider.get(null, function ( error, data ) {
                    if ( !error ) {
                        keyboard.$node.classList.remove('small');
                        if ( virtualInput ) {
                            input.changeView('small', 'remove');
                        } else {
                            input.$node.classList.remove('small');
                        }
                        keyboard.init({
                            data: data
                        });
                    }
                });
                break;
        }

        // show keyboard
        callback(false, {});
        document.body.style.display = 'none';

        setTimeout(function () {
            document.body.style.display = '';
            window.core.call('show');
            window.core.call('focus');
        }, 0);
    }
});

if ( !virtualInput ) {
    if ( PLATFORM === 'TIZEN' || PLATFORM === 'WEBOS' || PLATFORM === 'SMARTTV' ) {
        input.$node.setAttribute('readonly', 'true');
    }
}

window.core.plugins.keyboardLayouts.addListener('update', function ( list ) {
    var lang       = require('../modules/lang'),
        codes      = [],
        fullNames  = [],
        shortNames = [];

    if ( list && list.length > 0 ) {
        list.forEach(function ( code ) {
            var index = lang.supportedCodes.indexOf(code);

            if ( index !== -1 ) {
                codes.push(lang.supportedCodes[index]);
                fullNames.push(lang.supportedFullNames[index]);
                shortNames.push(lang.supportedShortNames[index]);
            }
        });

        lang.codes = codes;
        lang.fullNames = fullNames;
        lang.shortNames = shortNames;
    }

    lang.active = lang.codes.indexOf(core.environment.language);

    if ( lang.active === -1 ) {
        lang.active = 0;
    }

    if ( PLATFORM === 'MAG' ) {
        /* eslint new-cap: 0 */
        window.parent.gSTB.SetInputLang(lang.codes[lang.active]);
    }

    radioList.init({
        focusIndex: lang.active,
        size: lang.fullNames.length > 5 ? 5 : lang.fullNames.length,
        data: lang.fullNames.map(function ( item, index ) {
            return {
                state: lang.active === index,
                title: item + ' (' + lang.shortNames[index] + ')',
                value: index
            };
        })
    });

    qwertyProvider.get(null, function ( error, data ) {
        if ( !error ) {
            keyboard.init({data: data});
        }
    });
});


// public
module.exports = page;
