'use strict';

/* eslint new-cap: 0 */
var Page = require('stb-component-page'),
    Grid = require('stb-component-grid'),

    keys = require('stb-keys'),
    app = require('mag-app'),

    lang = require('../modules/lang'),

    qwertyProvider = require('../modules/keyboards/qwerty'),
    numericProvider = require('../modules/keyboards/numeric'),
    symbolsProvider = require('../modules/keyboards/symbols'),

    actions = require('../modules/keyboards/actions'),

    page = new Page({$node: window.pageQwerty}),

    lastIntent = null,

    /**
     * Keyboard grid
     * @type Grid
     */
    keyboard;


function clearInput () {
    var text = JSON.parse(window.top.stbWindowMgr.GetFocusedInputInfo()).result.text,
        counter = 0;

    if ( text && text.length > 0 ) {

        while (counter < text.length) {
            window.top.stbWebWindow.SendVirtualKeypress('BackSpace', 8);
            ++counter;
        }
    }
}


qwertyProvider.get(null, function ( badSituation, data ) {
    keyboard = new Grid({
        parent: page,
        $node: window.pageQwertyKeyboard,
        data: data,
        cycleX: true,
        cycleY: false,
        render: function ( $item, data ) {
            if ( typeof data.value === 'undefined' ) {
                $item.innerText = '';
            } else {
                $item.innerText = data.value;
            }

            if ( typeof data.className === 'undefined' ) {
                if ( $item.className.indexOf('focus') === -1 ) {
                    $item.className = 'item';
                } else {
                    $item.className = 'item focus';
                }
            } else {
                $item.className += ' ' + data.className;
            }
        },
        events: {
            focus: function () {
                // if ( input.$node.value.length > 0 ) {
                //     input.$node.focus();
                //     input.$node.selectionStart = input.$node.selectionEnd = lastInputCaretPosition;
                // } else {
                //     document.body.focus();
                // }
            },
            // overflow: function ( event ) {
            //     // if ( event.direction === keys.up ) {
            //     //     lastInputCaretPosition = input.getCaretPosition();
            //     //     ++verticalNavigation.active;
            //     //     verticalNavigation.list[verticalNavigation.active].focus();
            //     // }
            // },
            'click:item': function (event) {
                if (typeof event.$item.data.action !== 'undefined') {
                    switch (event.$item.data.action) {
                        case actions.numericKeyboard:
                            numericProvider.get(null, function ( error, data ) {
                                if ( !error ) {
                                    keyboard.$node.classList.add('small');
                                    page.$node.classList.add('small');
                                    keyboard.init({
                                        data: data
                                    });
                                    app.numpad = true;
                                    /* eslint new-cap: 0 */
                                    /* eslint max-len: 0 */
                                    window.top.stbEvent.onVkNeedReposition(JSON.parse(window.top.stbWindowMgr.GetFocusedInputInfo()).result.positionHint);
                                }
                            });
                            break;

                        case actions.symbolsKeyboard:
                            symbolsProvider.get(null, function ( error, data ) {
                                if ( !error ) {
                                    keyboard.$node.classList.remove('small');
                                    page.$node.classList.remove('small');
                                    keyboard.init({
                                        data: data
                                    });
                                    app.numpad = false;
                                    /* eslint new-cap: 0 */
                                    window.top.stbEvent.onVkNeedReposition(JSON.parse(window.top.stbWindowMgr.GetFocusedInputInfo()).result.positionHint);
                                }
                            });
                            break;

                        case actions.qwertyKeyboard:
                            qwertyProvider.get(null, function ( error, data ) {
                                if ( !error ) {
                                    keyboard.$node.classList.remove('small');
                                    page.$node.classList.remove('small');
                                    keyboard.init({
                                        data: data
                                    });
                                    app.numpad = false;
                                    window.top.stbEvent.onVkNeedReposition(JSON.parse(window.top.stbWindowMgr.GetFocusedInputInfo()).result.positionHint);
                                }
                            });
                            break;

                        case actions.uppercase:
                            qwertyProvider.toggleCase();
                            qwertyProvider.get(null, function ( error, data ) {
                                var xIndex, yIndex;

                                if ( !error ) {
                                    xIndex = keyboard.focusX;
                                    yIndex = keyboard.focusY;

                                    keyboard.init({data: data});
                                    keyboard.focusItem(keyboard.map[yIndex][xIndex]);
                                }
                            });

                            break;

                        case actions.changeLanguage:
                            // radioList.focus();
                            ++lang.active;
                            if (lang.codes.length === lang.active) {
                                lang.active = 0;
                            }
                            if (PLATFORM === 'MAG') {
                                window.parent.gSTB.SetInputLang(lang.codes[lang.active]);
                            }
                            qwertyProvider.get(null, function (error, data) {
                                var xIndex, yIndex;

                                if ( !error ) {
                                    xIndex = keyboard.focusX;
                                    yIndex = keyboard.focusY;

                                    keyboard.init({
                                        data: data
                                    });
                                    keyboard.focusItem(keyboard.map[yIndex][xIndex]);
                                }
                            });
                            break;

                        case actions.done:
                            window.top.stbWebWindow.SendVirtualKeypress('Enter', 13);
                            break;

                        case actions.backspace:
                            window.top.stbWebWindow.SendVirtualKeypress('BackSpace', 8);
                            break;
                        case actions.space:
                            window.top.stbWebWindow.SendVirtualKeypress('Space', 0);
                            break;
                        case actions.cancel:
                            window.top.stbWebWindow.close();
                            break;
                        case actions.clear:
                            clearInput();

                            break;
                        case actions.moveCaretToEnd:
                            window.top.stbWebWindow.SendVirtualKeypress('End', 35);
                            break;
                        case actions.moveCaretToLeft:
                            window.top.stbWebWindow.SendVirtualKeypress('Left', 37);
                            break;
                        case actions.moveCaretToStart:
                            window.top.stbWebWindow.SendVirtualKeypress('Home', 36);
                            break;
                        case actions.moveCaretToRight:
                            window.top.stbWebWindow.SendVirtualKeypress('Right', 39);
                            break;
                    }
                } else if (typeof event.$item.data.value !== 'undefined') {
                    window.top.stbWebWindow.SendVirtualKeypress(event.$item.data.value, 0);
                }
            }
        }
    });
});

page.addListener('show', function () {
    keyboard.focus();
    document.body.style.height = '100%';
    document.body.style.width = '100%';
    window.top.stbEvent.onVkNeedReposition(JSON.parse(window.top.stbWindowMgr.GetFocusedInputInfo()).result.positionHint);
    // window.top.stbWebWindow.SetGeometry(sizes.main.left, sizes.main.top, sizes.main.width, sizes.main.height);
});


