window.core.plugins.keyboardLayouts.addListener('update', function (list) {
    var lang = require('../modules/lang'),
        codes = [],
        fullNames = [],
        shortNames = [];

    if (list && list.length > 0) {
        list.forEach(function (code) {
            var index = lang.supportedCodes.indexOf(code);

            if (index !== -1) {
                codes.push(lang.supportedCodes[index]);
                fullNames.push(lang.supportedFullNames[index]);
                shortNames.push(lang.supportedShortNames[index]);
            }
        });

        lang.codes = codes;
        lang.fullNames = fullNames;
        lang.shortNames = shortNames;
    }

    lang.active = lang.codes.indexOf(core.environment.language);

    if (lang.active === -1) {
        lang.active = 0;
    }

    if (PLATFORM === 'MAG') {
        window.parent.gSTB.SetInputLang(lang.codes[lang.active]);
    }

    // radioList.init({
    //     focusIndex: lang.active,
    //     size: lang.fullNames.length > 5 ? 5 : lang.fullNames.length,
    //     data: lang.fullNames.map(function ( item, index ) {
    //         console.log(item)
    //         console.log(lang.shortNames[index])

    //         return {
    //             state: lang.active === index,
    //             title: item + ' (' + lang.shortNames[index] + ')',
    //             value: index
    //         };
    //     })
    // });

    qwertyProvider.get(null, function (error, data) {
        if ( !error ) {
            keyboard.init({data: data});
        }
    });
});


